import { createStatePersistence } from '@speedlo/hooks'
import React from 'react'

import { SelectControl } from '../../atoms/SelectControl'
import { FCompanyBranchFragment } from '../../graph/generated'
import { formatBranchName } from '../../helpers/formats'
import { useAutorunWhenSingleBranch } from '../../hooks/useAutorunWhenSingleBranch'
import { useDropdownFilter } from '../../hooks/useDropdownFilter'
import { useDropdownMulti } from '../../hooks/useDropdownMulti'
import { useUserModel } from '../../hooks/useUserModel'
import { i18n } from '../../i18n'
import { idProp } from '../../tools/ramda'

export const useBranchesFilterEditable = createStatePersistence<RoA<ID>>(
  'branches',
  [],
)

export const BranchesFilter = React.memo<NoChildren>(() => {
  const userModel = useUserModel()
  const [branches, setBranches] = useBranchesFilterEditable()

  useAutorunWhenSingleBranch(branch => {
    setBranches([branch.id])
  })

  const selectProps = {
    options: userModel.branches,
    placeholder: i18n.t`Filter branches...`,
    formatOptionLabel: formatBranchName,
    getOptionValue: idProp,
    hideOnSingleOption: true,
  }

  const dropdownProps = useDropdownMulti<FCompanyBranchFragment>({
    ...selectProps,
    onSelectionChange: setBranches,
    selected: branches,
  })

  const filterProps = useDropdownFilter<FCompanyBranchFragment>({
    getOptionFilter: formatBranchName,
  })

  return (
    <SelectControl<FCompanyBranchFragment>
      {...dropdownProps}
      {...filterProps}
      {...selectProps}
    />
  )
})

/**
 * Access to currently persisted list of branches for filtering purposes.
 *
 * By default it will use all user branches if none are currently set.
 * This behavior can be disabled with argument.
 *
 * @param withUserBranchesWhenEmpty
 */
export function useBranchesFilter(withUserBranchesWhenEmpty = true) {
  const userModel = useUserModel()
  let [branches] = useBranchesFilterEditable()
  if (branches.length === 0 && withUserBranchesWhenEmpty === true) {
    branches = userModel.branchesId
  }
  const key = branches.join('|')
  // eslint-disable-next-line react-hooks/exhaustive-deps
  return React.useMemo(() => branches, [key])
}
